export default{
	data(){
		return{
			show:false,
			mobile:'',
			pwd:''
		}
	},
	onLoad(e){
		this.mobile=e.mobile
	},
	methods:{
		loginveri(){
			uni.redirectTo({
				url:'/pages/login/loginVeri'
			})
		},
		submit(){
			var that=this
			var url='&r=api.member.account.login&mobile='+this.mobile+'&pwd='+this.pwd
			  this.$apiPost(url).then((res) =>{
				   uni.setStorageSync('userInfo',res.data)
				  uni.switchTab({
				  	url:'../tabBar/mine'
				  })
			  })
		},
		showpsd(){
			this.show=!this.show
		}
	}
}
